import list from "@/components/indexcomponents/list.vue"
const app = getApp();
export default {
	//这是全部订单的顶部导航
	data() {
		return {
			statusBar:0,
			items:"全部",
			TabCur: 0,
			scrollLeft: 0,
			Myorder:[
				"全部",
				"待付款",
				"待发货",
				"待收货",
				"已发货",
				"退款售后"
			],
			list:[
				{
					images:'/static/index/indexlist/1.jpg',
					describe:"梓画 床 实木床 单双人床新中式床1.8米1.5m高箱储物床婚床卧室精品家具",
					price:"50"
				}
			]
		}
	},
	methods: {
		tabSelect(e) {
			// console.log(e.currentTarget.dataset)
			let {id,items} = e.currentTarget.dataset
			this.items = items
			this.TabCur = id;
			this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60
		}
	},
	components:{
		list,
	},
	onLoad(option){
		// console.log(option.index)
		let orderindex = option.index
		if(orderindex){
			this.TabCur = orderindex
			// console.log(this.Myorder[orderindex])
			this.items = this.Myorder[orderindex]
		}else{
			this.TabCur = 0;
			this.items = this.Myorder[0]
		}
		this.statusBar = app.globalData.statusBar
	}
}
